body { 
  background: url(data:image/jpeg;base64,) no-repeat center center fixed;
  background-size: cover;
}

.navbar {
    background: #006064;
    min-height: 70px;
    position: fixed;
    top: 0;
    font-size: 18px;
    padding: 20px 0;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    color: #fff;
    z-index: 99;
    white-space: nowrap;
  }
  
  .navbar div {
    text-align: center;
    margin-bottom: 20px;
    width: 100%;
  }
  
  .navbar div:first-child {
    letter-spacing: 1px;
    font-weight: 900;
  }
  
  .pipe {
    margin: 0 10px;
  }
  
  .correct {
    -webkit-animation: correctGuess 0.5s;
            animation: correctGuess 0.5s;
  }
  .incorrect {
    -webkit-animation: incorrectGuess 0.5s;
            animation: incorrectGuess 0.5s;
  }
  
  @-webkit-keyframes incorrectGuess {
    0% {
      color: white;
    }
    25% {
      color: red;
    }
    75% {
      color: red;
    }
    100% {
      color: white;
    }
  }
  
  @keyframes incorrectGuess {
    0% {
      color: white;
    }
    25% {
      color: red;
    }
    75% {
      color: red;
    }
    100% {
      color: white;
    }
  }
  
  @-webkit-keyframes correctGuess {
    0% {
      color: white;
    }
    25% {
      color: green;
    }
    75% {
      color: green;
    }
    100% {
      color: white;
    }
  }
  
  @keyframes correctGuess {
    0% {
      color: white;
    }
    25% {
      color: green;
    }
    75% {
      color: green;
    }
    100% {
      color: white;
    }
  }
  
  @media (min-width: 576px) {
    .navbar {
      display: flex;
      position: fixed;
      justify-content: space-around;
      font-size: calc(10px + 14 * (100vw - 320px) / 800);
    }
  
    .navbar div {
      margin-bottom: 20px;
      text-align: center;
      width: 33.333%;
    }
  }
.icon {
    margin-top: 40px;
    margin-bottom: 40px;
    align-content: flex-start;
    justify-content: space-evenly;
    position: relative;
  }
  
  @media (min-width: 1100px) {
    .icon {
      padding: 0 100px;
      width: 1100px;
    }
  }
  
  .icon {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    -webkit-perspective: 1000px;
            perspective: 1000px;
  }
  
  .shake {
    -webkit-animation: shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shaker 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  }
  
  @-webkit-keyframes shaker {
    10%,
    90% {
      -webkit-transform: translate3d(-1px, 0, 0);
              transform: translate3d(-1px, 0, 0);
    }
  
    20%,
    80% {
      -webkit-transform: translate3d(2px, 0, 0);
              transform: translate3d(2px, 0, 0);
    }
  
    30%,
    50%,
    70% {
      -webkit-transform: translate3d(-4px, 0, 0);
              transform: translate3d(-4px, 0, 0);
    }
  
    40%,
    60% {
      -webkit-transform: translate3d(4px, 0, 0);
              transform: translate3d(4px, 0, 0);
    }
  }
  
  @keyframes shaker {
    10%,
    90% {
      -webkit-transform: translate3d(-1px, 0, 0);
              transform: translate3d(-1px, 0, 0);
    }
  
    20%,
    80% {
      -webkit-transform: translate3d(2px, 0, 0);
              transform: translate3d(2px, 0, 0);
    }
  
    30%,
    50%,
    70% {
      -webkit-transform: translate3d(-4px, 0, 0);
              transform: translate3d(-4px, 0, 0);
    }
  
    40%,
    60% {
      -webkit-transform: translate3d(4px, 0, 0);
              transform: translate3d(4px, 0, 0);
    }
  }
.card {
    border: 3px solid #6ac51f;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
    cursor: crosshair;
    height: 170px;
    margin: 15px;
    overflow: hidden;
    width: 170px;
    display: flex;
    align-content: flex-start;
    justify-content: space-evenly;
    position: relative;
  }
  
  .card:hover {
      box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
      -webkit-transform: scale(1.05);
              transform: scale(1.05);
      transition: 0.1s;
  }
  
  .card-img-top {
      border-radius: 0;
    }
    
    .card-height {
      height: 100% !important;
      width: 100% !important;
    }
.footer {
    background-color: #006064;
    box-shadow: 0 -14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    color: white;
    font-size: 18px;
    height: 50px;
    padding: 10px 0;
  }
